
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import {WriteMail} from './index'
import Send from './sendMailToFuture/WriteMail'
import Program from './Program'


export default {
directives: {
TransferDom
},
components: {
Panel,
'send': Send,
Group,
LoadMore,
Program,
Popup,
'writeMail': WriteMail
},
methods: {
onImgError (item, $event) {
console.log(item, $event)
},
gotoProgram (item) {
this.currentProgram = WriteMail
this.showProgram = true
this.programName = item.title
console.log(item.program)
},
backList () {
this.showProgram = false
}
},
data () {
return {
msg: '更多活动敬请期待',
currentProgram: WriteMail,
styleObj: {
'zIndex': 101
},
programName: '',
showProgram: false,
type: '1',
list: [{
src: require('../../assets/activity/sendMail/mail.jpeg'),
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '给未来写封信',
desc: '将你对未来的期望写入到区块链，让他永远的存在下去，未来你打开这个信息，希望你过的比现在好。',
program: 'writeMail'
}, {
src: require('../../assets/activity/duobao/duobao.jpeg'),
title: '开心夺宝',
desc: '平台提供各种奖品，每个奖品分配对应数量的号码，每个号码价值1个ISC币，当一件奖品所有号码售出后，根据既定的规则计算出一个幸运号码，持有该号码的用户，直接获得该奖品。',
program: 'duobao'
}]
}
}
}



